.mainwraper {
  display: flex;
}
.sidebar {
  width: 50px;
  height: 540px;

  background: #373b53;

  display: flex;
  flex-direction: column;
}
.sidebar__logo {
  background-image: url(../adds/toolLogo.png);

  width: 50px;
  height: 50px;
}
.sidebar__buttons {
  width: 50px;

  flex: 1 1 auto;
}
.sidebar__btn {
  width: 50px;
}
.sidebar__settings {
  width: 50px;
  height: 50px;
}

.mainwindow {
  display: flex;
  flex-direction: column;
}
.topbar {
  width: 910px;
  height: 50px;

  display: flex;
}
.topbar__search {
  width: 60%;
}
.topbar__account {
  width: 20%;
}
.topbar__buttons {
  width: 20%;
}
.content {
  background-color: #f2f3f7;
  /* width: 910px; */
  /* height: 490px; */

  padding-left: 30px;
  padding-right: 30px;
  display: flex;
  flex-direction: column;
}

.dashboard {
  height: 77px;
  display: flex;
  align-items: center;
}
.dashboard__text {
  flex: 1 1 auto;
}
.dashboard__btnwrap {
  margin-left: 15px;
}
.dashboard__btnborder {
  display: flex;
  border: solid 1px rgb(182, 31, 31);
  border-radius: 5px;
}
