.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.row_basics_auto {
  margin-top: 20px;
  flex: 1 1 auto;
}

.col {
  display: flex;
}

.col-1 {
  width: 8.33%;
}
.col-2 {
  width: 16.66%;
}
.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33%;
}
.col-5 {
  width: 41.66%;
}
.col-6 {
  width: 50%;
}
.col-7 {
  width: 58.33%;
}
.col-8 {
  width: 66.66%;
}
.col-9 {
  width: 75%;
}
.col-10 {
  width: 83.33%;
}
.col-11 {
  width: 91.66%;
}
.col-12 {
  width: 100%;
}

.column_1x1 {
  flex: 0 0 calc(395px / 2);
  height: 77px;
  background: #ffffff;
  box-shadow: 0 2px #ededf6;
  border-radius: 5px;
}
.column_2x2 {
  flex: 0 0 calc(830px / 2);
  height: calc(378px / 2);
  border: solid 1px red;

  background: #ffffff;
  box-shadow: 0 2px #ededf6;
  border-radius: 5px;
}
.column_3x1 {
  flex: 0 0 calc(395px / 2);
  height: 286px;
  border: solid 1px rgb(0, 0, 0);

  background: #ffffff;
  box-shadow: 0 2px #ededf6;
  border-radius: 5px;
}
.column_3x2 {
  flex: 0 0 calc(830px / 2);
  height: 286px;
}
.column_3x2 > .column_1x1 {
  margin-top: 20px;
}

